<template>
  <div class="container">
    <h1>My Todos</h1>
    <label for="create-todo">Create Todo</label>
    <input type="text" v-model="text" id="create-todo">
    <button @click="createTodo">Create</button>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <p v-for="todo in todos" :key="todo._id">
      {{todo.text}}
      <button @click="deleteTodo(todo._id)">Delete</button>
    </p>
    
  </div>
</template>

<script>
import TodosController from '../controllers/TodosController'

export default {
  name: 'TodosView',
  data() {
    return {
      todos: [],
      error: '',
      text: ''
    }
  },
  async created() {
    try {
      this.todos = await TodosController.getTodos();
    } catch (error) {
      this.error = error.message;
    }
  },
  methods: {
    async createTodo() {
      await TodosController.insertTodo(this.text)
      this.todos = await TodosController.getTodos();
    },
    async deleteTodo(id) {
      await TodosController.deleteTodo(id)
      this.todos = await TodosController.getTodos();
    }
  }
};
</script>

